import React from "react"
import { Link } from "gatsby"
import * as scss from "./RepoHeader.module.scss"
import Tags from "../data/tags.json"

const getTagByKey = (tagName) => {
  return Tags.find((t) => t.key === tagName) || false
}

const RepoHeader = (frontmatter) => {
  const { url, tags } = frontmatter.frontmatter
  const items = []

  for (const [index, value] of tags.entries()) {
    items.push(<Link to={`/repos#${value}`} key={index}><button class="bx--tag bx--tag--green"> <span class="bx--tag__label" title={getTagByKey(value).name}>{value}</span> </button></Link>)
  }

  return (
    <div className="bx--grid">
      <div className="bx--row">
        <div className="bx--col-lg-1">Repository: </div>
        <div className="bx--col-lg-4"><a href={url} target="_blank" rel="noreferrer">{url}</a></div>
      </div>
      <div className="bx--row">
        <div className="bx--col-lg-1 category-header">Categories:</div>
        <div className="bx--col-lg-4"><div className={scss.flex_sm}>{items}</div></div>
      </div>
    </div>
  )
}

export default RepoHeader



